.Educations {
	margin-top: 100px;
	h1 {
		text-align: center;
		text-transform: uppercase;
		font-size: 25px;
        margin-bottom: 40px;
	}

	.educations-box {
		display: flex;
		justify-content: center;
		img {
			width: 40%;
		}

		.educations-alt {
			width: 60%;
			.education-per {
				width: 100%;
				display: flex;
				justify-content: space-between;
				text-transform: uppercase;
				.institute-name {
					width: 80%;
					padding: 10px;
					font-weight: bolder;
                    margin: 0;
                    margin-bottom: 50px;
				}
				.degree-name {
                    font-weight: 200;
                    padding-top: 10px;
                    margin: 0;
				}
				.endtime {
					width: 20%;
					padding: 10px;
					font-weight: 500;
					color: rgb(87, 87, 87);
                    padding-top: 0px;
                    margin: 10px;
				}
			}
		}
	}
}


@media screen and (max-width : 700px) {
	.Educations{
		.educations-box{
			img{
				display: none;
			}
			.educations-alt{
				width: 100%;

				.education-per{
					flex-direction: column;
					.institute-name{
						width: 100%;
						padding: 0px;
						margin-bottom: 5px;

					}
					.degree-name{
						font-weight: 200!important;
						color: rgb(114, 114, 114);
						margin-top: 5px;
					}
					.endtime{
						margin: 0px;
						padding : 0px;
						width: 100%;
						margin-bottom: 40px;
						font-size: 14px;
						margin-top: 6px;
					}
				}
			}
		}
	}
}