.MenuBar {
  overflow: hidden;
  z-index: 1000;
  position: fixed;
  transition: 1s;
  width: 100%;
  .TopBar {
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    .logoTab {
      height: 100%;
      display: flex;
      align-items: center;
      padding-left: 40px;
      .logo1 {
        height: 50px;
      }
      .logo2 {
        height: 30px;
        margin-left: 10px;
      }
    }

    .menubars {
      margin-right: 50px;
      height: 25px;
      width: 25px;
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      transition: 0.1s;
    }
    .menusp {
      transition: 0.3s;
    }

    .bars {
      transition: 0.5s !important;
    }
    .bars.bar1 {
      transform: rotate(-45deg) translate(-9px, 6px);
      background-color: rgb(172, 172, 172) !important;
    }
    .bars.bar2 {
      opacity: 0;
    }
    .bars.bar3 {
      transform: rotate(45deg) translate(-8px, -7px);
      background-color: rgb(172, 172, 172) !important;
    }
    .bars {
      transition: 0.1s;
      margin: 0;
      width: 30px;
      height: 4px;
      background-color: black;
    }

    .menuclick {
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      p {
        margin-right: 15px;
        font-weight: bold;
        text-transform: uppercase;
        transition: 0.1s;
      }
      &:hover {
        .menubars {
          p {
            background-color: rgb(112, 112, 112);
          }
        }
        p {
          color: rgb(112, 112, 112);
        }
      }
    }
  }
}

.MenuBarX {
  transition: 0.7s;
  overflow: hidden;
  z-index: 1000;
  height: 100vh;
  background-color: #0f141e;
  position: fixed;
  width: 100%;
  .TopBar {
