  }

  .menusItems {
    // left: 50%;
    // top: 50%;
    // transform: translate(-50%,-50%);
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: 1s;
    ul {
      padding: 0;
      margin: 0;
      li {
        list-style: none;
        margin: 0;
        padding: 0;
        color: rgb(121, 121, 121);
        // font-family: 'Roboto', sans-serif;
        text-align: center;
        font-weight: 900;
        margin-top: 10px;
        transition: 0.4s all;
      }

      a {
        text-decoration: none;
        font-size: 47px;
        text-transform: capitalize;
        cursor: pointer;
        &:hover {
          li {
            color: white;
          }
        }
      }
    }
  }
  .menuheader {
    color: rgb(121, 121, 121);
    font-size: 20px;
    text-transform: uppercase;
    letter-spacing: 5px;
    margin-bottom: 20px;
    font-weight: 200;
  }
  .activenav li {
    color: white !important;
  }
}
.menusItems.close {
  transition: 1s;
  opacity: 0;
  ul a li {
    animation: menuitmesclose 1s linear;
  }
}
.menusItems.open {
  ul a li {
    animation: menuitems 1s linear;
  }
}
.MenuBarX.menuisclose {
  height: 0vh;
  transition: 0.5s;
}

@keyframes menuitems {
  0% {
    opacity: 0;
  }
  10% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes menuitmesclose {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@media screen and (max-width : 700px) {
  .MenuBar,
  .MenuBarX {
    .TopBar {
      .logoTab {
        padding-left: 10px;
        .logo1 {
          height: 40px;
        }
        .logo2 {
          height: 25px;
        }
      }

      .menubars {
        margin-right: 10px;
      }
    }
    .menusItems {
      margin-top: 50px;
      ul a li {
        font-size: 40px;
      }
      .menuheader {
        font-size: 16px;
        font-weight: 100;
      }
    }
  }

  .menu-footer {
    font-size: 8px;
    width: 300px;
  }
}
.menu-footer {
  position: absolute;
  top: 95%;
  left: 50%;
  transform: translateY(-95%) translateX(-50%);

  z-index: 1000;
  // margin-top: 50px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  letter-spacing: 4px;
  display: flex;
  color: #797979;
  P {
    font-weight: 100;
  }
  img {
    height: 20px;
    margin: 0px 10px;
  }
  @media screen and (max-width: 700px) {
    & {
      top: 90%;
      left: 50%;
      transform: translateY(-90%) translateX(-50%);
    }
  }
}

.social-media-icons {
  display: flex;
  justify-content: space-between;
  width: 200px;
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -90%);
  .icon-box {
    font-size: 20px;
    cursor: pointer;
    i {
      color: #797979;
      transition: 0.2s all;
    }
    &:hover {
      i {
        color: white;
      }
    }
  }
  @media screen and (max-width: 700px) {
    & {
      top: 85%;
      left: 50%;
      transform: translate(-50%, -85%);
    }
  }
}
