.Skills{
    margin-top: 100px;
    .items{
        display: grid;
        grid-template-columns: 25% 25% 25% 25%;
    }
    .item{
        img{
            height: 24px;
        }
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 100%;
        height: 80px;
        p{
            margin-top: 5px;
            color: rgb(66, 66, 66);
            text-transform: uppercase;
            font-size: 14px;
        }
      
    }

    h1{
        text-align: center;
        margin-bottom: 50px;
        text-transform: uppercase;
    }

    .skill{
        display: grid;
        grid-template-columns: 55% 45%;

        .right-img{
            width: 100%;
        }
    }
    p{
        padding: 0;
        margin: 0;
    }

    .abc{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}