.contact-me {
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 0px 40px;
  .contact {
    display: flex;
    flex-direction: column;
    border-right: 1px solid rgb(207, 207, 207);

    input,
    textarea,
    button {
      width: 80%;
      margin-top: 20px;
      height: 30px;
      border: 1px solid rgb(219, 219, 219);
      outline: none;
      padding-left: 10px;
    }
    textarea {
      height: 60px;
      padding: 10px;
      width: calc(80% - 8px);
    }

    button {
      width: calc(80% + 14px);
      color: white;
      background-color: rgb(56, 56, 56);
      height: 40px;
      cursor: pointer;
      margin-top: 40px;
    }
  }

  .details {
    padding-left: 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    .option {
      display: flex;
      align-items: center;
      margin-bottom: 30px;
      a {
        color: black;
        text-decoration: underline;
      }
      img {
        height: 25px;
        margin-right: 20px;
      }
    }
  }
}


.header-h1{
    text-align: center;
    font-size: 30px;
    text-transform: uppercase;
    font-weight: bolder;
    margin-bottom: 50px;
}