.Home {
  padding: 0px 40px;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  .SomeImportant {
    margin-top: 150px;
    width: 60%;
    p {
      margin: 0;
      padding: 0;
    }
    .name {
      font-weight: 600;
      font-family: sans-serif;
      font-size: 16px;
      text-transform: uppercase;
      letter-spacing: 6px;
      color: rgb(148, 148, 148);
    }

    .HeaderX {
      margin-top: 30px;
      font-size: 60px;
      font-family: "Helvetica Now Display DW", sans-serif;
      font-weight: 900;
    }

    .text {
      margin-top: 30px;
      font-size: 22px;
      font-family: "Helvetica Now Display DW", sans-serif;
      font-weight: 500;
      color: rgb(99, 99, 99);
      line-height: 38px;
    }

    .bottom {
      margin-top: 20px;
      a {
        margin-right: 20px;
        text-decoration: none;
      }

      .abc::before {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0;
        border-bottom: 1px solid rgb(58, 58, 58);
      }
      .abc:hover {
        color: rgb(105, 105, 105);
      }
    }
  }
}
.abc {
  font-weight: bolder;
  color: black;
  transition: 0.3s;
  position: relative;
}
.down-animation {
  height: 35px;
  width: 20px;
  border: 2px solid black;
  border-radius: 20px;
  position: relative;
  display: flex;
  justify-content: center;
  p {
    height: 5px;
    width: 5px;
    border-radius: 10px;
    background-color: black;
    position: absolute;
    animation: animate-up-down 2s infinite;
    margin: 0px;
  }
}
@keyframes animate-up-down {
  0% {
    top: 5px;
  }
  50% {
    top: 25px;
  }
  100% {
    top: 5px;
  }
}
.project-header {
  margin-top: 110px;
  p {
    color: rgb(155, 155, 155);
    margin: 0px;
    letter-spacing: 7px;
    margin-left: 6px;
  }
  p::selection {
    background-color: pink;
    color: white;
  }
  h1 {
    font-family: "Helvetica Now Display DW", sans-serif;
    font-weight: 900;
    letter-spacing: -0.01em;
    color: #0f141e;
    margin: 0px;
    font-size: 60px;
    margin-top: 20px;
  }
}

.Projects-grid {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 20px;
}

.ProfileImage {
  position: absolute;
  top: 100%;
  left: 100%;
  transform: translate(-100%, -100%);
  img {
    width: 660px;
  }
}
@media screen and (max-width: 700px) {
  .ProfileImage {
    display: none;
  }
  .Home {
    padding: 0px 20px;
    .SomeImportant {
      width: 100%;
      margin-top: 50px;
      .HeaderX {
        font-size: 40px;
      }
      .text {
        font-size: 20px;
      }
    }
    .bottom {
      padding-top: 10px;
    }
  }
}

.projects {
  padding: 0px 40px;
}

.ending-project {
  display: flex;
  flex-direction: column;
  align-items: center;
  p {
    font-size: 14px;
    font-family: "Helvetica Now Display DW", sans-serif;
    letter-spacing: 4px;
    margin: 0px;
    color: rgb(175, 175, 175);
    margin-top: 100px;
  }

  a {
    margin: 0px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    font-family: "Helvetica Now Display DW", sans-serif;
    font-weight: 800;
    border-bottom: 4px solid rgb(255, 255, 255);
    transition: 1s all;
    padding-bottom: 10px;
    cursor: pointer;
    font-size: 30px;
    text-decoration: none;
    color: black;

    svg {
      margin-left: 20px;
      margin-top: 5px;
    }

    &:hover {
      border-bottom: 4px solid rgb(0, 0, 0);
      transition: 1s all;
    }
  }
}


@media screen and (max-width : 700px) {
  .projects {
    .project-header{
      h1{
        font-size: 35px;
      }
      p{
        font-size: 10px;
        letter-spacing: 6px;
      }
    }

    .Projects-grid{
      grid-template-columns: 100%;
    }
  }
}