.casestudy {
  padding: 0px 40px;
  margin-top: 60px;
  padding-top: 30px;
  .case-study-header {
    color: rgb(143, 143, 143);
    font-size: 16px;
    font-family: "Helvetica Now Display DW", sans-serif;
    font-size: 14px;
    font-weight: 400;
    text-transform: uppercase;
  }
  .header-x {
    width: 600px;
    font-family: "Helvetica Now Display DW", sans-serif;
    margin-bottom: 40px;
  }
  .my-rule {
    margin: 0px;
    margin-top: 10px;
    font-family: "Helvetica Now Display DW", sans-serif;
    font-weight: 500;
    font-size: 14px;
  }
  .head {
    display: grid;
    grid-template-columns: 50% 50%;
  }
  .lorem-ispum {
    margin-top: 10px;
    font-size: 18px;
    color: rgb(151, 151, 151);
    line-height: 30px;
    margin-top: 50px;
    &::selection {
      background-color: rgb(196, 196, 196);
      color: white;
    }
  }

  .images {
    margin-top: 80px;
    width: 100%;
    .img {
      margin-top: 40px;
      width: 100%;
      img {
        width: 100%;
      }
    }
  }

  .ending {
    display: flex;
    justify-content: center;
    h1 {
      font-family: "Helvetica Now Display DW", sans-serif;
      font-weight: bold;
      text-transform: capitalize;
      font-size: 40px;
      border-bottom: 4px solid rgba(218, 218, 218, 0.507);
      margin-top: 80px;
    }
  }
}
