.Project {
    height: 7000px;
    width: calc(100% - 20px);
    overflow: hidden;
    position: relative;
    transition: 0.2s;
    cursor: pointer;
    .project-image {
      height: 100%;
      position: absolute;
      transition: 0.3s;
      z-index: 0;
      min-width: 100%;
    }
    .background {
      height: 100%;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      transition: 0.3s;
      z-index: 1;
    }
  
    .about {
      z-index: 10;
      position: absolute;
      top: 0;
      padding-left: 40px;
      padding-top: 50px;
  
      .timezone {
        letter-spacing: 10px;
      }
  
      .about-text {
        font-size: 30px;
        width: 80%;
      }
    }
    .case-study {
      font-size: 15px;
      display: flex;
      align-items: center;
      height: 20px;
      margin-top: 50px;
      text-transform: capitalize;
      margin-left: 2px;
      opacity: 0;
      transition: 0.4s;
      font-weight: bolder;
      svg {
        margin-left: 20px;
        padding-top: 2px;
      }
    }
    .used {
      display: grid;
      width: 60%;
      grid-template-columns: 25% 25% 25% 25%;
  
      .used-name {
        height: 30px;
        background-color: rgba(0, 0, 0, 0.301);
        width: 96%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
        border-radius: 30px;
        text-transform: lowercase;
        transition: 0.2s;
      }
    }
  
    &:hover {
      .project-image {
        transform: scale(1.03);
        transition: 0.3s;
      }
      .background {
        background-color: rgba(0, 0, 0, 0.767);
      }
  
      .about {
        .used-name {
          background-color: rgb(255, 255, 255);
          color: black;
          transition: 0.5s;
          font-weight: bolder;
        }
        .case-study {
          opacity: 1;
          margin-top: 30px;
        }
      }
    }
  }
  
  .Project {
    height: 700px;
  }
  